<template>
  <div class="member-card-block">
    <div class="member-card">
      <p class="member-type">{{ data.card.type }} Member Card</p>

      <div class="card-detail">
        <p><span>Name</span> : {{ data.name }}</p>
        <p><span>Phone No</span> : {{ data.phone }}</p>
        <p>
          <span>Purchased Amount</span> : {{ data.card.total_purchased }}MMK
        </p>
        <p>
          <span>Expired Date</span> :
          {{ $moment(data.card.expired_date).format("DD/MM/YYYY") }}
        </p>
      </div>
    </div>

    <div class="member-card-demo">
      <el-button
        v-ripple.400
        circle
        class="member-download no-width my-0 flat"
        icon="bx bxs-download"
        @click="generate()"
        type="primary"
      >
      </el-button>

      <p class="member-type-demo">{{ data.card.type }} Member Card</p>

      <div class="card-detail-demo">
        <p>Name : {{ data.name }}</p>
        <p>Phone No : {{ data.phone }}</p>
        <p>Purchased Amount : {{ data.card.total_purchased }}MMK</p>
        <p>
          Expired Date :
          {{ $moment(data.card.expired_date).format("DD/MM/YYYY") }}
        </p>
      </div>
      <img src="/image/card.png" class="card-img-demo" alt="" />
    </div>
  </div>
</template>

<script>
import domtoimage from "dom-to-image-more";

export default {
  props: {
    data: {},
  },
  data() {
    return {
      printCard: false,
    };
  },
  watch: {
    data: {
      handler: () => {},
      deep: true,
    },
  },
  methods: {
    generate() {
      this.printCard = true;
      setTimeout(() => {
        const node = document.querySelector(".member-card");
        domtoimage
          .toPng(node, {})
          .then((dataUrl) => {
            const link = document.createElement("a");
            link.href = dataUrl;
            link.setAttribute("download", this.data.name + "-member-card.png");
            document.body.appendChild(link);

            link.click();
            if (
              window.ReactNativeWebView &&
              window.ReactNativeWebView.postMessage
            ) {
              window.ReactNativeWebView.postMessage(dataUrl);
            }

            this.printCard = false;
          })
          .catch(() => {});
      }, 500);
    },
  },
};
</script>

<style scoped>
* {
  font-family: "Alkalis" !important;
  color: rgb(54, 54, 54);
}
.member-type {
  font-size: 150px !important;
  margin-top: 16%;
}
.card-detail {
  margin-top: 100px;
  margin-left: -365px;
}
.card-detail * {
  font-family: sans-serif !important;
  font-size: 75px !important;
  margin-bottom: 20px;
}
.card-detail > p > span {
  color: rgb(var(--primary)) !important;
}
.member-card {
  position: fixed;
  background-image: url("/image/card.png");
  /* top: 0px;
  left: 0px;
  transform: scale(0.2);
  left: -800px; */
  z-index: -100000;
  width: 2560px;
  height: 1440px;
  padding: 15px;
  color: #000;
  overflow: hidden;
  border-radius: 80px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.member-type-demo {
  font-size: 15px !important;
  margin-top: 43px;
  z-index: 1;
}
.card-detail-demo {
  margin-top: 12px;
  margin-left: -24px;
  z-index: 1;
}
.card-detail-demo > p {
  font-family: sans-serif !important;
  font-size: 8px !important;
  z-index: 1;
}
img.card-img-demo {
  position: absolute;
  width: 100%;
}
.member-card-demo {
  margin-top: 25px;
  margin-bottom: 10px;
  position: relative;
  background-size: 100%;
  background-repeat: no-repeat;
  width: 256px !important;
  height: 144px !important;
  color: #000;
  overflow: hidden;
  border-radius: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.member-card-block {
  display: flex;
  justify-content: center;
}
.member-download {
  position: absolute !important;
  z-index: 5 !important;
  top: 10px !important;
  right: 10px !important;
}
</style>